nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid #000;
    padding-bottom: 20px;
}

nav a {
    color: gray;
    text-transform: uppercase;
}

@media (max-width: 400px) {
    nav a {
        font-size: 0.8em;
    }
    nav img {
        max-width: 120px;
    }
}
