body {
    overflow-x: hidden;
}

section.detail {
    padding: 80px 0;
    display: grid;
    grid-template-columns: 1fr 1fr;
}

section.detail .left-detail {
    position: relative;
}

section.detail .left-detail img {
    width: 70%;
}

section.detail .text-left-detail {
    position: absolute;
    background-color: #fff;
    top: 0;
    right: 0px;
    padding: 30px;
    max-width: 250px;
}

section.detail .text-left-detail h1 {
    font-size: 3em;
}

section.detail .text-left-detail p {
    margin-top: 20px;
    color: gray;
}

section.detail .left-detail button {
    position: absolute;
    left: 20px;
    bottom: 20px;
    background-color: rgba(0, 0, 0, 0.5);
    color: white;
    padding: 15px;
    border: none;
    cursor: pointer;
    text-transform: uppercase;
    opacity: 0.8;
}

section.detail .left-detail button img {
    width: 16px;
    margin-right: 15px;
}

section.detail img.artist {
    height: auto;
    width: 130px;
    position: absolute;
    bottom: -40px;
    right: 30px;
}

section.detail .right-detail {
    position: relative;
}

section.detail .right-detail .text-right-detail {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    margin: 0 20%;
    color: gray;
    letter-spacing: 1px;
    font-weight: bold;
}

section.detail .right-detail .text-right-detail a {
    color: gray;
    margin-top: 20px;
}

section.detail .right-detail .text-right-detail h1 {
    position: absolute;
}

section.detail .right-detail .year-detail {
    position: absolute;
    top: 15px;
    left: 20%;
    font-size: 10em;
    z-index: -1;
    color: #f3f3f3;
}

.progress-bar {
    width: 100vw;
    height: 2px;
    margin-left: -40px;
}

section.detail div.modal {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}
section.detail div.modal button {
    color: white;
    position: relative;
    top: -42%;
    left: 40%;
    border: solid 1px white;
    padding: 10px;
    background-color: transparent;
    cursor: pointer;
}

section.detail div.modal img {
    max-height: 80%;
    margin-left: -40px;
}

footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 20px;
}

footer .foot {
    width: 20%;
    display: flex;
    justify-content: space-between;
}

footer .foot img {
    width: 28px;
    height: 28px;
    cursor: pointer;
}

@media (max-width: 1200px) {
    section.detail .right-detail .year-detail {
        top: -80px;
    }
}

@media (max-width: 1000px) {
    section.detail .left-detail img.hero {
        width: 80%;
    }
    section.detail img.artist {
        right: 60px;
        bottom: 0px;
    }
    section.detail {
        grid-template-columns: 1fr;
    }
    section.detail .left-detail img.hero {
        width: 60%;
    }
    section.detail .right-detail .text-right-detail {
        margin: 60px 0;
    }
    section.detail .right-detail .year-detail {
        font-size: 5em;
        left: 80%;
        top: 60px;
    }
    section.detail div.modal img {
        max-width: 80%;
    }
    section.detail .text-left-detail {
        right: 40px;
    }
}

@media (max-width: 700px) {
    section.detail .text-left-detail {
        max-width: 150px;
        right: 20px;
    }
    section.detail .text-left-detail h1 {
        font-size: 1.5em;
    }
    section.detail .left-detail img.hero {
        width: 100%;
    }
    section.detail .text-left-detail {
        position: inherit;
    }
    section.detail img.artist {
        right: 0;
        bottom: 10px;
    }
    section.detail .right-detail .year-detail {
        left: 60%;
    }
    section.detail .left-detail button {
        left: 20px;
        bottom: 30%;
    }
}

@media (max-width: 500px) {
    section.detail .right-detail .year-detail {
        left: 30%;
    }
    section.detail .left-detail button {
        bottom: 45%;
    }
    section.detail div.modal img {
        max-height: auto;
        max-width: 90%;
        margin-left: -60px;
    }
}
