.cards {
    margin-top: 40px;
    column-count: 4;
    gap: 30px;
}

.cards .card img {
    width: 100%;
}

.cards .card {
    margin-bottom: 30px;
    position: relative;
}

.cards .card .text-card {
    position: absolute;
    left: 10px;
    bottom: 10px;
    color: white;
    font-size: 0.8em;
    text-shadow: 1px 1px #000;
}

.cards .card .text-card h2 {
    margin-bottom: 10px;
}

@media (max-width: 1000px) {
    .cards {
        column-count: 3;
    }
}

@media (max-width: 800px) {
    .cards {
        column-count: 2;
    }
}

@media (max-width: 500px) {
    .cards {
        column-count: 1;
    }
}
